html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background-color: #FFF;
}

.container {
  width: fit-content;
  max-width: 1440px;
  min-height: 100vh;
  margin: 0 auto;
}

.main {
  display: flex;
  transition: all 0.5s cubic-bezier(0.95, -0.36, 0, 1.32);
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main {
    flex-direction: column;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .main {
    flex-direction: column;
  }
}
.main_image {
  width: 505px;
  height: 330px;
  padding: 95px 47.5px 95px 230px;
  transition: all 0.5s cubic-bezier(0.95, -0.36, 0, 1.32);
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main_image {
    height: 100%;
    padding: 0;
    width: 100%;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .main_image {
    padding: 0;
    width: 100%;
  }
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .main_image {
    padding: 25px 20px;
  }
}
@media screen and (min-width: 1025px) and (max-width: 1439px) {
  .main_image {
    padding: 95px 47.5px 95px 10%;
  }
}
.main_content {
  padding: 135px 230px 135px 47.5px;
  color: #121212;
  font-family: "Roboto", sans-serif;
  transition: all 0.5s cubic-bezier(0.95, -0.36, 0, 1.32);
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main_content {
    padding: 30px 10px;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .main_content {
    padding: 20px 20px 0 20px;
  }
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .main_content {
    padding: 50px 20px 0 20px;
  }
}
@media screen and (min-width: 1025px) and (max-width: 1439px) {
  .main_content {
    padding: 135px 10% 135px 47.5px;
  }
}
.main_content_title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  pointer-events: none;
  transition: all 0.5s cubic-bezier(0.95, -0.36, 0, 1.32);
}
@media screen and (min-width: 769px) and (max-width: 850px) {
  .main_content_title {
    font-size: 30px;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1200px) {
  .main_content_title {
    font-size: 30px;
  }
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main_content_title {
    text-align: center;
  }
}
.main_content_paragraph {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin-top: 24px;
  pointer-events: none;
  transition: all 0.5s cubic-bezier(0.95, -0.36, 0, 1.32);
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main_content_paragraph {
    text-align: center;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .main_content_paragraph {
    width: 80%;
  }
}
@media screen and (min-width: 769px) and (max-width: 850px) {
  .main_content_paragraph {
    font-size: 14px;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1100px) {
  .main_content_paragraph {
    font-size: 14px;
  }
}
.main_content_button {
  padding: 16px 40px;
  background: #E4C1AE;
  border-radius: 32px;
  border: none;
  color: #FFF;
  outline: none;
  margin-top: 50px;
  transition: transform 0.3s cubic-bezier(1, 0.21, 0.4, 1.26), width 0.6s cubic-bezier(1, 0.21, 0.4, 1.26);
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main_content_button {
    margin-top: 40px;
    width: 100%;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .main_content_button {
    margin-top: 20px;
  }
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .main_content_button {
    margin-top: 35px;
  }
}
.main_content_button:hover {
  cursor: pointer;
  transform: scale(1.2);
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .main_content_button:hover {
    transform: none;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .main_content_button:hover {
    transform: none;
  }
}

@keyframes appear {
  from {
    transform: scale(0) translate(-50%, -50%);
  }
  to {
    transform: scale(1) translate(-50%, -50%);
  }
}
.modal {
  display: none;
  position: fixed;
  top: 50%;
  width: 920px;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  transform-origin: top left;
  font-family: "Roboto", sans-serif;
  color: #121212;
  background-color: #FFF;
  z-index: 2;
  border-radius: 5px;
  transition: all 0.5s cubic-bezier(0.95, -0.36, 0, 1.32);
  animation: appear 1s cubic-bezier(0.19, 1, 0.22, 1) normal;
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .modal {
    width: 80%;
    height: 90vh;
    padding: 10px;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .modal {
    width: 70%;
    padding: 10px;
  }
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .modal {
    width: 90%;
  }
}
.modal_title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  width: fit-content;
  margin: 56px 0 0 95px;
  pointer-events: none;
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .modal_title {
    margin: 50px auto 0;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .modal_title {
    margin: 25px 0 0 5%;
  }
}
.modal_paragraph {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  width: fit-content;
  margin: 24px 0 56px 95px;
  pointer-events: none;
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .modal_paragraph {
    margin: 50px auto 0;
    text-align: center;
  }
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .modal_paragraph {
    margin: 25px 0 25px 5%;
  }
}
.modal_paragraph_counter {
  font-weight: 800;
}
.modal_close {
  position: absolute;
  right: 46px;
  top: 46px;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 36px;
  font-weight: 700;
  padding: 0;
  line-height: 12px;
}
@media screen and (min-width: 0) and (max-width: 425px) {
  .modal_close {
    right: 10%;
    top: 5%;
  }
}
.modal_close:hover {
  cursor: pointer;
  transform: scale(1.2);
}
.modal_reset {
  position: absolute;
  bottom: 25px;
  width: 80%;
  border: none;
  border-radius: 25px;
  background-color: #ddd;
  left: 50%;
  transform: translateX(-50%);
  padding: 15px;
  transition: transform 0.2s ease-in;
  display: none;
}
@media screen and (min-width: 426px) and (max-width: 768px) {
  .modal_reset {
    position: relative;
    width: 100%;
    transform: translateX(0);
    left: 0;
    bottom: 0;
  }
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .modal_reset {
    position: relative;
    width: 20%;
    left: 95px;
    transform: translateX(0);
  }
}
@media screen and (min-width: 1025px) and (max-width: 1439px) {
  .modal_reset {
    position: relative;
    width: 20%;
    left: 95px;
    transform: translateX(0);
  }
}
@media screen and (min-width: 1440px) {
  .modal_reset {
    position: relative;
    width: 20%;
    left: 95px;
    transform: translateX(0);
  }
}
.modal_reset:hover {
  cursor: pointer;
  transform: translateX(0) scale(1.02);
}

.overlay {
  background-color: #121212;
  opacity: 0.5;
  display: none;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.show {
  display: block;
}

.confetti {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 2;
  display: none;
}